                            </td>
                            <td>{{data?data.wrnTrsCntPer:""}}</td>
                            <td>{{data?data.wrnTrsCntOrg:""}}</td>
                            <td>{{data?data.wrnTrsCntPro:""}}</td>
                            <td>{{data?data.wrnTrsCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
