                            </td>
                            <td>{{data?data.wrnAccCntPer:""}}</td>
                            <td>{{data?data.wrnAccCntOrg:""}}</td>
                            <td>{{data?data.wrnAccCntPro:""}}</td>
                            <td>{{data?data.wrnAccCntAll:""}}</td>
                    </tr>


                    <tr>
                        <td :rowspan="3">确认信息</td>
                            <td>
                                确认预警客户数
                            </td>
                            <td>{{data?data.cfmCstCntPer:""}}</td>
                            <td>{{data?data.cfmCstCntOrg:""}}</td>
                            <td>{{data?data.cfmCstCntPro:""}}</td>
                            <td>{{data?data.cfmCstCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                确认预警交易笔数
                            </td>
                            <td>{{data?data.cfmTrsCntPer:""}}</td>
                            <td>{{data?data.cfmTrsCntOrg:""}}</td>
                            <td>{{data?data.cfmTrsCntPro:""}}</td>
                            <td>{{data?data.cfmTrsCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                确认预警交易金额
                            </td>
                            <td>{{data?data.cfmAccCntPer:""}}</td>
                            <td>{{data?data.cfmAccCntOrg:""}}</td>
                            <td>{{data?data.cfmAccCntPro:""}}</td>
                            <td>{{data?data.cfmAccCntAll:""}}</td>
                    </tr>


                    <tr>
                        <td :rowspan="3">排除信息</td>
                            <td>
                                排除预警客户数
                            </td>
                            <td>{{data?data.eclCstCntPer:""}}</td>
                            <td>{{data?data.eclCstCntOrg:""}}</td>
                            <td>{{data?data.eclCstCntPro:""}}</td>
                            <td>{{data?data.eclCstCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                排除预警交易笔数
                            </td>
                            <td>{{data?data.eclTrsCntPer:""}}</td>
                            <td>{{data?data.eclTrsCntOrg:""}}</td>
                            <td>{{data?data.eclTrsCntPro:""}}</td>
                            <td>{{data?data.eclTrsCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                排除预警交易金额
                            </td>
                            <td>{{data?data.eclAccCntPer:""}}</td>
                            <td>{{data?data.eclAccCntOrg:""}}</td>
                            <td>{{data?data.eclAccCntPro:""}}</td>
                            <td>{{data?data.eclAccCntAll:""}}</td>
                    </tr>



                    <tr>
                        <td :rowspan="11">报告信息</td>
                            <td>
                                向反洗钱中心报送大额交易份数
                            </td>
                            <td>{{data?"0":""}}</td>
                            <td>{{data?"0":""}}</td>
                            <td>{{data?"0":""}}</td>
                            <td>{{data?"0":""}}</td>
                    </tr>
                    <tr>
                            <td>
                                其中：补正份数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
                    <tr>
                            <td>
                                向反洗钱中心报送可疑交易份数
                            </td>
                            <td>{{data?data.rep00CntPer:""}}</td>
                            <td>{{data?data.rep00CntOrg:""}}</td>
                            <td>{{data?data.rep00CntPro:""}}</td>
                            <td>{{data?data.rep00CntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                其中：补正份数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
                    <tr>
                            <td>
                                其中：报送重点可疑交易份数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
                    <tr>
                            <td>
                                向人行及分支机构报送涉案报告份数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
                    <tr>
                            <td>
                                向公安机关直接报案线索数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
                    <tr>
                            <td>
                                报送客户数
                            </td>
                            <td>{{data?data.rptCstCntPer:""}}</td>
                            <td>{{data?data.rptCstCntOrg:""}}</td>
                            <td>{{data?data.rptCstCntPro:""}}</td>
                            <td>{{data?data.rptCstCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                报送交易笔数
                            </td>
                            <td>{{data?data.rptTrsCntPer:""}}</td>
                            <td>{{data?data.rptTrsCntOrg:""}}</td>
                            <td>{{data?data.rptTrsCntPro:""}}</td>
                            <td>{{data?data.rptTrsCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                报送交易金额
                            </td>
                            <td>{{data?data.rptAccCntPer:""}}</td>
                            <td>{{data?data.rptAccCntOrg:""}}</td>
                            <td>{{data?data.rptAccCntPro:""}}</td>
                            <td>{{data?data.rptAccCntAll:""}}</td>
                    </tr>
                    <tr>
                            <td>
                                已立案数
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                    </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
      this.formValidate.date1 = [this.getFormatDate(), this.getFormatDate()];
  },
  data() {
    return {
      formValidate: {
        date1: ["",""]
      },
      data: null,
      options1: {
          disabledDate (date) {
              return date && date.valueOf() > Date.now()-24*60*60*1000;
          }
      },
       ruleValidate: {
          date1: [
            {
              required: true,
              validator: (rule, value, callback) => {
                if (this.formValidate.date1[0] == "") {
                  return callback(new Error("请选择时间"));
                } else {
                  callback();
                }
              },
              trigger: "on-change"
            }
        ],
       },
      tableBody: [
        {
          name: "证券经纪",
          busItems: [
            {
              id:"1",
              busNm: "A股",
              score: "100",
              dept: "零售-运营管理部",
            },
            {
              id:"2",
              busNm: "B股",
              score: "90",
              dept: "零售-运营管理部",
            },
            {
              id:"3",
              busNm: "创业板股票",
              score: "80",
              dept: "零售-衍生品经纪业务部",
            }
          ]
        },
        {
          name: "证券承销与保荐",
          busItems: [
            {
              id:"4",
              busNm: "深市港股通",
              score: "100",
              dept: "零售-财富管理部",
            },
            {
              id:"5",
              busNm: "沪市港股通",
              score: "90",
              dept: "零售-财富管理部",
            }
          ]
        }
      ]
    };
  },
  mounted() {},
  methods: {
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
        if (!this.data) {
            this.$Message.warning("暂无数据");
            return;
        }
        let data = {
          start: this.formValidate.date1[0],
          end: this.formValidate.date1[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/aml/doExportAmlStatInfo", data, {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          })
          .then(response => {
              this.download(response,'导出文档.xls');
          })
          .catch(error => {});
    },
    getFormatDate() {
        var date = new Date(new Date()-24*60*60*1000);
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
    },
    handleSubmit(){
      let valid1 = "";
      this.$refs["formValidate"].validate(valid => {
          valid1 = valid;
      });
      if (!valid1)  return;
        let data = {
          start: this.formValidate.date1[0],
          end: this.formValidate.date1[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/aml/getAmlStatInfo", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code=="200"){
                  if (!response.data){
                    this.$Message.info("请稍后尝试查询，正在执行中");
                  }
                  this.data = response.data;
            }
          })
          .catch(error => {});
    }
  },
  watch: {}
};
</script>
<style lang="scss" @scoped>
.tableBox2{
  tbody{
    td{
      border: 1px solid #dcdee2;
    }
  }
}
</style>


